import React from "react";
import {Formik, Field, Form} from 'formik';
import {loginFormAPI} from "../components/API/Api";
import {connect} from "react-redux";
import {loginThunk} from "../redux/authReducer";
import {Redirect} from "react-router-dom";
import * as yup from "yup";


 const LoginForm = (props) => {

     const validationSchema = yup.object().shape({
         email: yup.string().email().typeError('Введите вашу почту').required('Введите вашу почту'),
     })


    if (props.isAuth){
        return <Redirect to={'/profile'} />
    }

return  <div>
        <h1>Login</h1>
        <Formik
            initialValues={{
                email: '',
                password: '',
                rememberMe: false,
                captcha: false
            }}
            onSubmit={async (values) => {
                loginFormAPI.authLogin(values).then(props.loginThunk())
                validationSchema={validationSchema}



            }}
        >
            {({values,errors,touched}) => (
                <Form>
                    <label htmlFor="email">Логин</label>
                    <Field name="email" placeholder="Введите вашу почту" type="email"/>
                    {errors.email && touched.email ?
                        <div >{errors.email}</div> : null }

                    <label htmlFor="password">Пароль</label>
                    <Field name="password" placeholder="Введите ваш пароль" type='password'/>

                    <label htmlFor="rememberMe">
                        <Field type="checkbox" name="rememberMe"/>
                        Запомнить меня
                    </label>
                    <button type={"submit"}> Войти</button>

                </Form>
            )}
        </Formik>
    </div>
}

let mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth
    }

}


export default connect(mapStateToProps, {loginThunk})(LoginForm)
